import React from 'react';
import Box from '@mui/material/Box';
import { useTranslation } from 'react-i18next';

const Formation = () => {
  const { t } = useTranslation();

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        height: '100vh',
        overflow: 'auto',
        backgroundColor: '#000000',
        minWidth: '100%'
      }}
    >
        <h1>{t('FOMATION_TITLE')}</h1>
    </Box>
  );
};

export default Formation;