import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import List from '@mui/material/List';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import { styled } from '@mui/material/styles';

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialog-paper': {
    backgroundColor: 'white', // Couleur de fond de la pop-up
  },
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
  },
}));
function MediaCard() {

  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const reactProject = (
    <Card onClick={handleClickOpen} sx={{ maxWidth: 345,  margin:'20px', borderRadius: 3,
    transition: '0.2s','&:hover': {transform: 'scale(1.05)'}}}>
      <CardMedia
        sx={{ height: 140 }}
        image = 'https://blog.wildix.com/wp-content/uploads/2020/06/react-logo.jpg'
        title="green iguana"
      />
      <CardContent sx={{backgroundColor: '#252525',}} >
        <Typography gutterBottom variant="h5" component="div" color="white" sx={{backgroundColor: '#252525',}}>
          React application
        </Typography>
        <Typography variant="body2" color="white">
          Lizards are a widespread group of squamate reptiles, with over 6,000
          species, ranging across all continents except Antarctica
        </Typography>
      </CardContent>
      <CardActions sx={{backgroundColor: '#151515',}}>
        <Button size="small">Share</Button>
        <Button size="small">Learn More</Button>
      </CardActions>
     </Card> 
  );

  const Java = (
    <Card sx={{ maxWidth: 345, margin:'20px', borderRadius: 3,
    transition: '0.2s','&:hover': {transform: 'scale(1.05)'}}}>
      <CardMedia
        sx={{ height: 140 }}
        image = 'https://blog.wildix.com/wp-content/uploads/2020/06/react-logo.jpg'
        title="green iguana"
      />
      <CardContent sx={{backgroundColor: '#252525',}} >
        <Typography gutterBottom variant="h5" component="div" color="white" sx={{backgroundColor: '#252525',}}>
          Java RPG
        </Typography>
        <Typography variant="body2" color="white">
          Lizards are a widespread group of squamate reptiles, with over 6,000
          species, ranging across all continents except Antarctica
        </Typography>
      </CardContent>
      <CardActions sx={{backgroundColor: '#151515',}}>
        <Button size="small">Share</Button>
        <Button size="small">Learn More</Button>
      </CardActions>
     </Card> 
  );

  const Python = (
    <Card sx={{ maxWidth: 345, margin:'20px', borderRadius: 3,
    transition: '0.2s','&:hover': {transform: 'scale(1.05)'}}}>
      <CardMedia
        sx={{ height: 140 }}
        image = 'https://blog.wildix.com/wp-content/uploads/2020/06/react-logo.jpg'
        title="green iguana"
      />
      <CardContent sx={{backgroundColor: '#252525',}} >
        <Typography gutterBottom variant="h5" component="div" color="white" sx={{backgroundColor: '#252525',}}>
          Python casse-brique
        </Typography>
        <Typography variant="body2" color="white">
          Lizards are a widespread group of squamate reptiles, with over 6,000
          species, ranging across all continents except Antarctica
        </Typography>
      </CardContent>
      <CardActions sx={{backgroundColor: '#151515',}}>
        <Button size="small">Share</Button>
        <Button size="small">Learn More</Button>
      </CardActions>
     </Card> 
  );

  const Internationalisation = (
    <Card sx={{ maxWidth: 345, margin:'20px', borderRadius: 3,
    transition: '0.2s','&:hover': {transform: 'scale(1.05)'}}}>
      <CardMedia
        sx={{ height: 140 }}
        image = 'https://blog.wildix.com/wp-content/uploads/2020/06/react-logo.jpg'
        title="green iguana"
      />
      <CardContent sx={{backgroundColor: '#252525',}} >
        <Typography gutterBottom variant="h5" component="div" color="white" sx={{backgroundColor: '#252525',}}>
          Internationalisation Angular Application
        </Typography>
        <Typography variant="body2" color="white">
          Lizards are a widespread group of squamate reptiles, with over 6,000
          species, ranging across all continents except Antarctica
        </Typography>
      </CardContent>
      <CardActions sx={{backgroundColor: '#151515',}}>
        <Button size="small">Share</Button>
        <Button size="small">Learn More</Button>
      </CardActions>
     </Card> 
  );
    return (
      <React.Fragment>
        <List sx={{display:"flex", flexWrap:"wrap", justifyContent: 'space-between'}}>
            {reactProject}
            {Java}
            {Python}
            {Internationalisation}
        </List>
        <BootstrapDialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <DialogTitle sx={{ m: 0, p: 2 }} id="customized-dialog-title">
          Modal title
        </DialogTitle>
        <IconButton
          aria-label="close"
          onClick={handleClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
        <DialogContent dividers>
          <Typography gutterBottom>
            Cras mattis consectetur purus sit amet fermentum. Cras justo odio,
            dapibus ac facilisis in, egestas eget quam. Morbi leo risus, porta ac
            consectetur ac, vestibulum at eros.
          </Typography>
          <Typography gutterBottom>
            Praesent commodo cursus magna, vel scelerisque nisl consectetur et.
            Vivamus sagittis lacus vel augue laoreet rutrum faucibus dolor auctor.
          </Typography>
          <Typography gutterBottom>
            Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus
            magna, vel scelerisque nisl consectetur et. Donec sed odio dui. Donec
            ullamcorper nulla non metus auctor fringilla.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleClose}>
            Save changes
          </Button>
        </DialogActions>
      </BootstrapDialog>
        </React.Fragment>
    );
};

export default MediaCard;
