import React from 'react';
import Box from '@mui/material/Box';

const About = () => {

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        height: '100vh',
        overflow: 'auto',
        backgroundColor: '#000000',
        minWidth: '100%'
      }}
    >
        <h1>A PROPOS</h1>
    </Box>
  );
};

export default About;
