import React from 'react';
import Box from '@mui/material/Box';
import Images from './images';


const Home = () => {

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        height: '100vh',
        overflow: 'auto',
        backgroundColor: '#000000',
        minWidth: '100%'
      }}
    >
          <Images></Images>
          <h1>Home page</h1>
    </Box>
  );
};

export default Home;